import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import DialogContent from "@mui/material/DialogContent";

const dispatchedEvents = [];

const SrsMuiDialogContent = function (props, ref) {
  return (
    <DialogContent {...props} theme={props.muiTheme}>
      <slot />
    </DialogContent>
  );
};

const styles = `
  /* Custom styles for the MuiDialogContent component */
`;

const props = [
  {
    name: "dividers",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to DialogContent if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiDialogContent),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
